import * as React from 'react';
import { Mutation } from 'react-apollo';
import gql from 'graphql-tag';

export const LOGOUT_MUTATION = gql`
  mutation LOGOUT_MUTATION {
    logout
  }
`;

export const Logout = () => (
  <Mutation mutation={LOGOUT_MUTATION}>
    {logout => (
      <button
        onClick={async () => {
          const res = await logout();
          console.log(res);
        }}
      >
        Logout
      </button>
    )}
  </Mutation>
);
